<template>
  <transition name="list-fade">
    <div class="playlist" v-show="showFlag" @click="hide">
      <div class="list-wrapper" @click.stop>
        <div class="list-header">
          <h1 class="title">
            <i class="icon" :class="iconMode" @click="changeMode"></i>
            <span class="text">{{ modeText }}</span>
            <span class="clear" @click="showConfirm">
              <div class="icon-clear"></div>
            </span>
          </h1>
        </div>
        <scroll
          class="list-content"
          :data="sequenceList"
          ref="listContent"
          :refreshDelay="refreshDelay"
        >
          <transition-group name="list" tag="ul">
            <li
              class="item"
              v-for="(item, index) in sequenceList"
              :key="item.rid"
              @click="selectItem(item, index)"
              ref="listItem"
            >
              <i class="current" :class="getCurrentIcon(item)"></i>
              <span class="text">{{ item.name }}</span>
              <span class="like" @click.stop="toggleFavorite(item)">
                <i :class="getFavoriteIcon(item)"></i>
              </span>
              <span class="delete" @click.stop="deleteOne(item)">
                <i class="icon-delete"></i>
              </span>
            </li>
          </transition-group>
        </scroll>
        <div class="list-operate">
          <div class="add" @click="addSong">
            <i class="icon-add"></i>
            <span class="text"></span>
          </div>
        </div>
        <div class="list-close" @click="hide">
          <span></span>
        </div>
      </div>
      <confirm ref="confirm" text="" confrimBtnText="" @confirm="confirmClear" />
      <add-song ref="addSong"></add-song>
    </div>
  </transition>
</template>

<script type="ecmascript-6">
import AddSong from 'components/add-song/add-song'
import { mapActions } from 'vuex'
import { playMode } from 'common/js/config'
import Scroll from 'base/scroll/scroll'
import Confirm from 'base/confirm/confirm'
import { playerMixin } from 'common/js/mixin'

export default {
  mixins: [playerMixin],
  components: {
    Scroll,
    Confirm,
    AddSong
  },
  data() {
    return {
      showFlag: false,
      refreshDelay: 100
    }
  },
  computed: {
    /* eslint-disable */
    modeText() {
      return this.mode === playMode.sequence
        ? ''
        : this.mode === playMode.random
        ? ''
        : ''
    }
    /* eslint-enable */
  },
  watch: {
    currentSong(newSong, oldSong) {
      if (!this.showFlag || newSong.rid === oldSong.rid) return false
      this.scrollToCurrent(newSong)
    }
  },
  methods: {
    show() {
      this.showFlag = true
      setTimeout(() => {
        this.$refs.listContent.refresh()
        this.scrollToCurrent(this.currentSong)
      }, 20)
    },
    hide() {
      this.showFlag = false
    },
    selectItem(item, index) {
      if (this.mode === playMode.random) {
        index = this.playlist.findIndex(song => song.rid === item.rid)
      }
      this.setCurrentIndex(index)
      this.setPlayingState(true)
    },
    deleteOne(item) {
      this.deleteSong(item)
      if (!this.playlist.length) {
        this.hide()
      }
    },
    addSong() {
      this.$refs.addSong.show()
    },
    getCurrentIcon(item) {
      if (this.currentSong.rid === item.rid) {
        return 'icon-play'
      }
      return ''
    },
    showConfirm() {
      this.$refs.confirm.show()
    },
    confirmClear() {
      this.deleteSongList()
      this.hide()
    },
    scrollToCurrent(current) {
      const index = this.sequenceList.findIndex(
        list => list.rid === current.rid
      )
      this.$refs.listContent.scrollToElement(this.$refs.listItem[index], 300)
    },
    ...mapActions(['deleteSong', 'deleteSongList'])
  }
}
</script>

<style lang="stylus" scoped>
@import '~common/stylus/variable'
@import '~common/stylus/mixin'

.playlist
  position fixed
  left 0
  right 0
  top 0
  bottom 0
  z-index 200
  background-color $color-background-d
  &.list-fade-enter-active, &.list-fade-leave-active
    transition opacity 0.3s
    .list-wrapper
      transition all 0.3s
  &.list-fade-enter, &.list-fade-leave-to
    opacity 0
    .list-wrapper
      transform translate3d(0, 100%, 0)
  &.list-fade-enter, .list-wrapper
    position absolute
    left 0
    bottom 0
    width 100%
    background-color $color-highlight-background
    .list-header
      position relative
      padding 20px 30px 10px 20px
      .title
        display flex
        align-items center
        .icon
          margin-right 10px
          font-size 30px
          color $color-theme-d
        .text
          flex 1
          font-size $font-size-medium
          color $color-text-l
        .clear
          extend-click()
          .icon-clear
            font-size $font-size-medium
            color $color-text-d
    .list-content
      max-height 240px
      overflow hidden
      .item
        display flex
        align-items center
        height 40px
        padding 0 30px 0 20px
        overflow hidden
        &.list-enter-active, &.list-leave-active
          transition all 0.1s
        &.list-enter, &.list-leave-to
          height 0
        .current
          flex 0 0 20px
          width 20px
          font-size $font-size-small
          color $color-theme-d
        .text
          flex 1
          no-wrap()
          font-size $font-size-medium
          color $color-text-d
        .like
          extend-click()
          margin-right 15px
          font-size $font-size-small
          color $color-theme
          .icon-favorite
            color $color-sub-theme
        .delete
          extend-click()
          font-size $font-size-small
          color $color-theme
    .list-operate
      width 140px
      margin 20px auto 30px auto
      .add
        display flex
        align-items center
        padding 8px 16px
        border 1px solid $color-text-l
        border-radius 100px
        color $color-text-l
        .icon-add
          margin-right 5px
          font-size $font-size-small-s
        .text
          font-size $font-size-small
    .list-close
      text-align center
      line-height 50px
      background $color-background
      font-size $font-size-medium-x
      color $color-text-l
</style>
